import React, { useState, useEffect } from 'react'
import { CButton, CModal, CModalHeader, CModalTitle, CModalFooter, CModalBody } from '@coreui/react'
import PropTypes from 'prop-types'
const ComfirmModal = (props) => {
  const [visible, setVisible] = useState(false)
  useEffect(() => {
    setVisible(props.isShow)
  }, [props.isShow])

  return (
    <>
      <CModal alignment="center" visible={visible} onClose={props.closeErrorModal}>
        <CModalHeader>
          <CModalTitle>狀態更變</CModalTitle>
        </CModalHeader>
        <CModalBody>確定要將狀態改成{props.content}?</CModalBody>
        <CModalFooter>
          <CButton color="secondary" onClick={props.closeErrorModal}>
            Close
          </CButton>
          <CButton color="primary" onClick={props.handleChangeStatus}>
            確定
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}

ComfirmModal.propTypes = {
  isShow: PropTypes.bool,
  closeErrorModal: PropTypes.func,
  handleChangeStatus: PropTypes.func,
  content: PropTypes.string,
}

export default ComfirmModal
