import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import CIcon from '@coreui/icons-react'
import { cilFindInPage } from '@coreui/icons'
import { CContainer, CRow, CCol, CButton } from '@coreui/react'
import { reviewLists } from '../../mocks/quotationData'
import Pagenate from '../Share/Pagenate'
import ComfirmModal from '../Share/ComfirmModal'

const QuotationReview = () => {
  const [isShowModal, setShowModal] = useState(false)
  const [data, setData] = useState([])
  const [currentPage, setCurrentPage] = useState(0)
  const [totalPages, setTotalPages] = useState(0)
  const itemsPerPage = 10

  useEffect(() => {
    // axios.get('https://jsonplaceholder.typicode.com/posts/').then((response) => {
    setData(reviewLists)
    setTotalPages(Math.ceil(reviewLists.length / itemsPerPage))
    // })
  }, [])

  const startIndex = currentPage * itemsPerPage
  const endIndex = startIndex + itemsPerPage
  const subset = data.slice(startIndex, endIndex)
  const handlePageChange = (selectedPage) => {
    setCurrentPage(selectedPage.selected)
  }

  const handleClick = () => {
    setShowModal(true)
  }

  return (
    <div className="lists-container px-3">
      <CContainer>
        <div className="list-head">
          <CRow>
            <CCol sm={2}>報價單編號</CCol>
            <CCol sm={2}>客戶名稱</CCol>
            <CCol sm={2}>客戶統編</CCol>
            <CCol sm={2}>建立時間</CCol>
            <CCol sm={1}>狀態</CCol>
            <CCol className="d-flex justify-content-center" sm={1}>
              操作
            </CCol>
            <CCol className="d-flex justify-content-center" sm={2}>
              預覽/下載
            </CCol>
          </CRow>
        </div>
        <div className="list-body loa-list">
          {subset &&
            subset.length > 0 &&
            subset.map((item, i) => {
              return (
                <div className="list-item" key={item.num + i}>
                  <CRow className="align-items-center">
                    <CCol sm={2}>{item.num}</CCol>
                    <CCol sm={2}>{item.client}</CCol>
                    <CCol sm={2}>{item.taxID}</CCol>
                    <CCol sm={2}>{item.createDate}</CCol>
                    <CCol sm={1}>{item.statusText}</CCol>
                    <CCol className="d-flex justify-content-center" sm={1}>
                      <CButton color="warning" onClick={() => handleClick()}>
                        核准
                      </CButton>
                    </CCol>
                    <CCol className="d-flex justify-content-center" sm={2}>
                      <Link to="/pdfContainer" className="action-icon">
                        <CIcon icon={cilFindInPage} />
                      </Link>
                    </CCol>
                  </CRow>
                </div>
              )
            })}
        </div>
        {data.length > itemsPerPage && (
          <Pagenate
            totalPages={totalPages}
            currentPage={currentPage}
            handlePageChange={handlePageChange}
          />
        )}
      </CContainer>
      <ComfirmModal
        isShow={isShowModal}
        closeErrorModal={() => setShowModal(false)}
        handleChangeStatus={() => setShowModal(false)}
        content="已核准"
      />
    </div>
  )
}

export default QuotationReview
